*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  background-color: #7918f2;
  color: #fff;
}
.coin-app{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: #fff;
}
.coin-search{
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.coin-text{
  margin-bottom: 32px;
  text-align: center;
}
.coin-input{
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(
    -225deg,
    #ac32e4 0%,
    #7918f2 48%,
    #4801ff 100%
  );

  color: #e2e2e2;
}

.coin-input::placeholder {
  color: #e2e2e2;
}
.row{
  float: left; /* for IE */
  display: table-row;
}
.row * {
  float: left; /* for IE */
  display: table-cell;
}
.row tr{
  font-size: 20px;
  width: 150px;
  position:relative;
  padding: 0;
  bottom: -70px;
}
.Sy{
  left: 99px;
}
.Pr{
  left: 130px;
}
.Vo{
  left: -68px;
}
.Pc{
  left: 250px;
}
.Ma{
  left: 88px;
}